@import 'scss/variables.scss';

.item__button {
	background-color: transparent;
	position: relative;

	&:hover {
		svg {
			path {
				stroke: $blue;
			}
		}
	}

	&:active {
		svg {
			path {
				stroke: rgba(0, 110, 219, 0.5);
			}
		}
	}

	&-popup {
		position:absolute;
		right: 25px;
		top: 20px;
		padding: 20px;
		background-color: #fff;
		border-radius: 10px;
		box-shadow: 2px 2px rgba(33, 41, 43, 0.1);
		z-index: 2;

		.remove {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			padding: 10px;
			border: 1px solid rgba(198, 0, 0, 0.5);
			border-radius: 10px;

			&__icon {
				width: 25px;
				height: 25px;
				margin-right: 5px;
			}

			&__text {
				color: $red;
				font-size: 20px;
			}

			&:hover {
				border: 1px solid rgba(198, 0, 0, 1);
				background-color: rgba(198, 0, 0, 0.1);
			}

			&:active {
				background-color: rgba(198, 0, 0, 0.2);
			}
		}

		.close {
			display: flex;
			align-items: center;
			opacity: 0.8;
			padding: 10px;
			border: 1px solid rgba(86, 97, 108, 0.5);
			border-radius: 10px;

			&__icon {
				width: 25px;
				height: 25px;
				margin-right: 5px;
			}

			&__text {
				color: $black;
				font-size: 20px;
			}

			&:hover {
				border: 1px solid rgba(86, 97, 108, 1);
				background-color: rgba(86, 97, 108, 0.2);
			}

			&:active {
				background-color: rgba(86, 97, 108, 0.4);
			}
		}
	}
}